.about-main {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 60px 80px 80px 80px;
    background-color: #ffffff;
}

.about-main-left,
.about-main-right {
    width: 50%;
    height: auto;
}
.section-container {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}

.about-main-right {
    display: flex;
    justify-content: flex-end;
    
}

.about-main-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-link-element {
    color: rgb(255, 255, 255);
}

.about-sub-head {
    margin: 0;
}

.about-details {
    margin: 10px 0 32px 0;
    margin-right: 40px;
}

.about-anime {
    height: auto;
    width: 100%;
    object-fit: contain;
}

.vector-frame {
    position: absolute;
    z-index: -10;
    left: 0;
    bottom: 0;
    width: 55%;
}

.about-vector {
    display: block;
    max-height: 100%;
    max-width: 100%;
}

@media only screen and (max-width: 640px) {
    .vector-frame {
        width: 90%;
    }

    .about-head {
        width: 80%;
    }

    .about-head > p {
        font-size: 1.2em;
        line-height: 1.2em;
    }

    .about-main {
        padding: 50px 32px;
    }

    .about-main-left,
    .about-main-right {
        width: 100%;
    }
}
